<template>
  <div class="sort">
    <div class="sorts">分类</div>
    <div class="box">
      <div class="all">
        <p>男生</p>
        <ul class="clearfixed">
          <li v-for="(item,index) in datas.data.male" :key="index">{{item.name}}</li>
        </ul>
      </div>
      <div class="all">
        <p>女生</p>
        <ul class="clearfixed">
          <li v-for="(item,index) in datas.data.female" :key="index">{{item.name}}</li>
        </ul>
      </div>
      <div class="all">
        <p>出版</p>
        <ul class="clearfixed">
           <li v-for="(item,index) in datas.data.press" :key="index">{{item.name}}</li>
        </ul>
      </div>
    </div>

    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("indexModule");

const API_PROXY = "https://bird.ioliu.cn/v1/?url=";

export default {
  name: "sort",

  computed: {
    ...mapState(["datas"])
  },
  methods: {
    //调出方法名
    ...mapMutations(['results'])
  },

  created() {
    this.axios
      .get(API_PROXY + "http://api.zhuishushenqi.com/cats/lv2/statistics")
      .then(result => {
        // console.log("result ==> ", result);
        
        //传值result
        this.$store.commit('indexModule/results',result)     
        //  console.log("datas.data==>",this.datas.data)
      })
      .catch(err => {
        // console.log("err ==> ", err);
      });

  }
};
</script>

<style lang="less" scoped>
.sort {
  .sorts {
    height: 40px;
    background: #3b454d;
    text-align: center;
    line-height: 40px;
    color: #f5f5f5;
    font-size: 14px;
  }
  .box {
    position: fixed;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 1.33333rem;
    overflow-y: auto;
    background-color: #f2f2f2;
    padding: 10px 10px 0;
    .all {
      > p {
        height: 30px;
        line-height: 30px;
        border-bottom: 1px solid #d6d2d2;
        box-shadow: 0 0 5px 6px inset #d6d2d2;
        margin: 0 20px;
        text-align: center;
        font-size: 14px;
        font-weight: bold;
        color: #806f6f;
      }
      > ul {
        padding: 10px 20px 0;
        > li {
          float: left;
          width: 60px;
          text-align: center;
          height: 30px;
          line-height: 30px;
          background: #fff;
          margin: 0 22px 10px;
          border-radius: 10px;
          background-color: #d6d2d2;
          box-shadow: 0 0 6px 2px #8c8585;
          margin-bottom: 13px;
        }
      }
    }
  }
}
</style>